.signup-container {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.signup-background {
    width: 100vw;
    display: grid;
    overflow: hidden;
    align-items: center;
    justify-content: space-evenly;
}

.signup-img img {
    width: 100vw;
    height: 100vh;
}

.signup-img .img {
    transform-origin: center center;
    animation: space-move 20s linear infinite;
    
    max-height: 80%;
}

.signup-img .mobile-img {
    display: none;
}


@keyframes space-move {
    0% {
        transform: scale(1);
    }

    100% {
        transform: scale(2);
    }
}


.signup-form {
    display: grid;
    position: absolute;
    right: 0;
    left: 0;
    justify-content: space-evenly;
    align-items: center;
    gap: 2.5rem;
    border-radius: 10px;
    transition: all 0.5s;
}

.signup-heading {
    font-size: 1.8rem;
    color: rgb(70, 90, 242);
    font-weight: 600;
    display: flex;
    justify-content: space-evenly;
}

.signup-text {
    display: grid;
    gap: 2rem;
    color: white;
}

.input-text {
    font-size: 1rem;
    display: flex;
    gap: 1rem;
    justify-content: space-between;
}

.input-text input {
    padding: 0.2rem 0 0.2rem 1rem;
    border-radius: 5px;
    outline: none;
    border: 1px solid transparent;
    background-color: white;
}

.input-text input:focus {
    border-color: green;
    background-color: rgb(116, 207, 198);
    color: white;
}

.check-box {
    display: flex;
    gap: 0.5rem;
    accent-color: rgb(21, 171, 21);
}

.check-box p {
    display: flex;
    justify-content: space-evenly;
}

.signup-btn {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.signup-btn button {
    width: 100%;
    font-size: 1rem;
    padding: 0.3rem 2rem;
    border: 1px solid transparent;
    color: white;
    border-radius: 5px;
    background-color: rgb(0, 166, 255);
}

.signup-btn button:hover {
    background-color: rgb(20, 204, 245);
}

@media (width < 650px) {
    .signup-img .img {
        display: none;
    }

    .signup-img .mobile-img {
        padding: 1rem;
        overflow: hidden;
        display: flex;
        height: 100vh;
        width: fit-content;
        transform: rotate(90deg);
        transform-origin: center center;
        animation: space-move 20s linear infinite;
        
    }
}

@media (width < 500px) {
    .signup-form {
        width: 100%;
        justify-content: space-evenly;
    }

    .signup-text {
        display: grid;
        gap: 2rem;
    }

    .input-text {
        display: grid;
    }

    .input-text input {
        width: 100%;
    }

    .signup-btn {
        padding-bottom: 1rem;
    }
    .check-box{
        display: flex;
        justify-content: space-evenly;
    }
}