footer{
    margin-top: 4rem;
}

.container6{
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 2rem;
}
li , .foo a{
    list-style: none;
    text-decoration: none;
    color: black;
    transition: all ;
}

.foo a:hover{
    color: rgb(87, 237, 169);
    transition-duration: 0.1s;
}

.wrapper1{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    justify-content: space-evenly;
    gap: 2rem;
}

.footer1 ul{
    display: grid;
    gap: 1rem;
}

.logo-icon img{
    max-width: 100px;
}

.icons ul{
    display: flex;
    gap: 0.5rem;
}

.icons li a img{
    max-width: 20px;
}

.informations{
    display: flex;
}

@media (width < 1025px){
    .wrapper1{
        display: grid;
        grid-template-columns: repeat(3,1fr);
    }
}

@media (width < 800px){
    .wrapper1{
        font-size: 0.8rem;
        padding: 1rem;
    }
}

@media (width < 600px){
    footer{
        font-size: 0.9rem;
    }
    .container6{
        display: grid;
        grid-template-columns: repeat(1,1fr);
    }
    .wrapper1{
        display: grid;
        grid-template-columns: repeat(2,1fr);
        justify-content: space-evenly;
        padding: 3rem;
    }
    .wrapper2{
        padding: 2rem;
    }
    .copyrights{
        padding: 1.4rem;
    }
    .rights p{
        padding: 0;
    }
}